import React, { useState } from "react";
import "./../global.scss";
import "./../app.scss";
import { Link, graphql } from "gatsby";
import Flipbox from "../components/Flipbox";

export const pagesQuery = graphql`
  query MyQuery {
    allFile(filter: { extension: { eq: "mdx" } }) {
      edges {
        node {
          relativePath
          id
        }
      }
    }
  }
`;

const App = ({ data }) => {
  return (
    <main>
      <div style={{ height: "100vh", flexDirection: "column" }}>
        <h1 className="huge">This is my blog of... amazing stuff</h1>
      </div>
      <div style={{ justifyContent: "flex-end" }}>
        <h2 className="huge">Latest</h2>
      </div>
      {data.allFile.edges.map(({ node }) => {
        console.log(node.id);
        return (
          <Flipbox
            flipContent={"FIRST POST"}
            frontContent={
              <Link
                to={
                  "/" + node.relativePath.slice(0, node.relativePath.length - 4)
                }
              >
                Read
              </Link>
            }
            button={"Hover to preview"}
          />
        );
      })}
    </main>
  );
};

export default App;
